import { useTranslation } from "react-i18next";
import style from "../../PlateformUser/PlateformUser.module.css";
import DocumentReminderStudentCard from "./DocumentReminderStudentCard";
import { useDocumentReminderStudentList } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import Loading from "../../../Loader/Loading";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyInfo from "../../../../Loader/EmptyInfo";
import { useEffect, useState } from "react";
const DocumentReminderStudent = ({ onClose, extra, onAction }) => {
  const { t } = useTranslation();
  const [list, setList] = useState([]);
  const [selectedId, setSelectedId] = useState([]);
  const [isAllSelect, setIsAllSelect] = useState(false);

  const { documentReminderStudentList, documentReminderStudentListLoading } =
    useDocumentReminderStudentList({
      data: {
        appId: extra?.appId,
        page: 1,
        limit: 2000,
        search: "",
      },
      skip: !extra?.appId,
    });

  useEffect(() => {
    setSelectedId(extra?.selectedId);
  }, [extra?.selectedId?.length]);

  useEffect(() => {
    if (documentReminderStudentList?.list?.length > 0) {
      setList(documentReminderStudentList?.list);
    }
  }, [documentReminderStudentList?.list]);
  const onSelectStudent = (val) => {
    if (selectedId?.includes(val)) {
      setSelectedId((pre) => pre?.filter((id) => id !== val));
    } else {
      setSelectedId((prev) => [...prev, val]);
    }
  };
  const onRemoveAll = () => {
    setSelectedId([]);
    setIsAllSelect(false);
  };
  const onSelectAll = () => {
    let a = [];
    for (let stu of list) {
      a.push(stu?._id);
    }
    setSelectedId(a);
    setIsAllSelect(true);
  };

  const onSearchEvent = (e) => {
    if (e.target.value) {
      let searchData = getStudentSearch(list, e.target.value);
      setList(() => searchData);
    } else {
      setList(() => list);
    }
  };

  return (
    <Modal>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>
                {selectedId?.length > 0 ? `${selectedId?.length} ` : ""}
                {t("select_student")}
              </h6>
            </div>
            <div className={style.modal_container_header}>
              {isAllSelect ? (
                <CreateButton
                  customStyle={{
                    margin: 0,
                    padding: "0.2rem 0.5rem",
                  }}
                  onAction={onRemoveAll}
                  label="remove_all"
                />
              ) : (
                <CreateButton
                  customStyle={{
                    margin: 0,
                    padding: "0.2rem 0.5rem",
                  }}
                  onAction={onSelectAll}
                  label="select_all"
                />
              )}
              <CreateButton
                customStyle={{
                  margin: 0,
                  padding: "0.2rem 0.5rem",
                }}
                onAction={() => onAction(selectedId)}
                label="next"
              />
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
                className={style.close_icon}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>
          {list?.map((stu) => (
            <DocumentReminderStudentCard
              key={stu?._id}
              stu={stu}
              selectedId={selectedId}
              onSelectStudent={onSelectStudent}
            />
          ))}

          {documentReminderStudentListLoading ? <Loading /> : null}
          {!documentReminderStudentListLoading && list?.length < 1 && (
            <EmptyInfo
              customStyleContainer={{
                marginTop: "9rem",
              }}
              title={"No student found."}
            />
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default DocumentReminderStudent;
const getStudentSearch = (list = [], search) => {
  return list.filter((stu) => {
    let name =
      stu?.studentFirstName ??
      "" + " " + stu?.studentMiddleName ??
      "" + " " + stu?.studentLastName ??
      "";
    if (
      name?.toLowerCase()?.includes(search?.toLowerCase()) ||
      stu?.studentGRNO?.toLowerCase()?.includes(search?.toLowerCase())
    ) {
      return stu;
    }
    return null;
  });
};
