import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Loading from "../../../Loader/Loading";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  useAdmissionApplicationRequiredDocumentExport,
  useAdmissionDocumentList,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import RequiredDocumentCard from "./RequiredDocumentCard";
import AddRequiredDocument from "./AddRequiredDocument";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import TransferSelectOngoingAppllication from "../OneOngoingAdmission/TransferApplication/TransferSelectOngoingAppllication";
import DocumentReminderStudent from "./DocumentReminderStudent";
import DocumentReminderMessage from "./DocumentReminderMessage";
const RequiredDocument = ({ aid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [extra, setExtra] = useState({
    appId: "",
    selectedId: [],
  });
  const [admissionApplicationRequiredDocumentExport] =
    useAdmissionApplicationRequiredDocumentExport();

  const {
    admissionDocumentList,
    admissionDocumentListLoading,
    admissionDocumentListRefetch,
  } = useAdmissionDocumentList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionDocumentList?.list?.length) {
      let obj = nextPage(admissionDocumentList?.list?.length);
      setState(() => obj);
    }
  }, [admissionDocumentList?.list]);

  const onSearchEvent = (e) => {
    if (
      !admissionDocumentListLoading &&
      admissionDocumentList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    admissionDocumentListRefetch();
  }, [admissionDocumentListRefetch]);

  const onExportList = (app) => {
    if (app?._id) {
      console.info("asfvsghfbna", app);
      setDisabled((pre) => !pre);
      onCloseStep();
      admissionApplicationRequiredDocumentExport({
        appId: app?._id,
      })
        .then(() => {
          let url =
            getQuery.state?.urlOffSet === "Institute"
              ? `/q/${params.username}/institute/member/admission?type=export`
              : `/q/${params.username}/member/staff/admission?type=export`;
          navigate(url, {
            state: getQuery.state,
          });
        })
        .catch({});
    }
  };

  const onCloseStep = () => {
    setStep("");
  };

  const onDocumentAPplication = (val) => {
    setExtra((prev) => ({ ...prev, appId: val?._id }));
    setStep(3);
  };

  const onStudentListGrab = (list) => {
    setExtra((prev) => ({ ...prev, selectedId: list }));
    setStep(4);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("required_documents")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "320px",
                    }}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                  />
                </div>
              </section>
              <CreateButton
                label="add_new_documents_only"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
              <CreateButton
                label="export"
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                  width: "fit-content",
                }}
                onAction={() => setStep(1)}
              />
              <CreateButton
                label="document_reminder"
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                  width: "fit-content",
                }}
                onAction={() => setStep(2)}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper>
          {admissionDocumentList?.list?.map((document, index) =>
            admissionDocumentList?.list?.length === index + 1 ? (
              <div key={document?._id} ref={ref}>
                <RequiredDocumentCard
                  document={document}
                  aid={aid}
                  onRefetch={onRefetch}
                />
              </div>
            ) : (
              <RequiredDocumentCard
                document={document}
                key={document?._id}
                aid={aid}
                onRefetch={onRefetch}
              />
            )
          )}
          {admissionDocumentListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            admissionDocumentList?.list?.length === 0 && (
              <EmptyMessage title="No document found related this search." />
            )
          ) : (
            admissionDocumentList?.list?.length === 0 && (
              <EmptyMessage title="No document." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddRequiredDocument
          aid={aid}
          onClose={onToggle}
          onRefetch={onRefetch}
        />
      )}
      {step === 1 ? (
        <TransferSelectOngoingAppllication
          onSelect={onExportList}
          aid={aid}
          onClose={() => setStep(1)}
        />
      ) : step === 2 ? (
        <TransferSelectOngoingAppllication
          onSelect={onDocumentAPplication}
          aid={aid}
          onClose={onCloseStep}
          selectedId={extra.appId}
        />
      ) : step === 3 ? (
        <DocumentReminderStudent
          onAction={onStudentListGrab}
          onClose={() => setStep(2)}
          extra={extra}
        />
      ) : step === 4 ? (
        <DocumentReminderMessage
          onSelect={onDocumentAPplication}
          aid={aid}
          onClose={() => setStep(3)}
          extra={extra}
          onCloseParent={onCloseStep}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default RequiredDocument;
