import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FeesStructure/FeesStructure.module.css";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  assestsUserFinanceUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useDocumentReminderSendStudent } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import FormFile from "../../../Ui/Input/FormFile";
const DocumentReminderMessage = ({ onClose, extra, aid, onCloseParent }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [sendNotify, setSendNotify] = useState({
    alarm_mode: "APP_NOTIFICATION",
    content: "",
    m_title: "",
    m_doc: "",
  });
  const [file, setFile] = useState("");
  const [fileUpload] = useFileUpload();
  const [documentReminderSendStudent] = useDocumentReminderSendStudent();
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setFile({
            name: e.target.files[0]?.name,
            key: res.data?.imageKey,
            type: e.target.files[0]?.type,
          });
          setSendNotify((prev) => ({
            ...prev,
            m_doc: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAction = () => {
    let validation = requiredField(sendNotify, ["content", "m_doc"]);
    if (existingRequiredField(validation)) {
      if (sendNotify.alarm_mode && extra?.selectedId?.length > 0 && aid) {
        setDisabled((pre) => !pre);
        documentReminderSendStudent({
          aid: aid,
          alarm_mode: sendNotify.alarm_mode,
          bodyParameter: {
            all_arr: extra?.selectedId,
            content: sendNotify.content,
            title: sendNotify.m_title ?? "",
            doc: sendNotify.m_doc ?? null,
          },
        })
          .then(() => {
            setDisabled((pre) => !pre);
            onCloseParent();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("student_message")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <section
              className={style.notify_container}
              onClick={() =>
                setSendNotify((prev) => ({
                  ...prev,
                  alarm_mode: "APP_NOTIFICATION",
                }))
              }
            >
              <img
                src={
                  sendNotify.alarm_mode === "APP_NOTIFICATION"
                    ? `${assestsUtilityUrl}/radio-blue.svg`
                    : `${assestsUtilityUrl}/radio-grey.svg`
                }
                alt="icon"
              />
              <div className={style.notify_text}>
                <h6>{t("Notify_with_In_app_Notification")}</h6>
                <p>{t("Notify_with_In_app_Notification_note")}</p>
              </div>
            </section>
            <section
              className={style.notify_container}
              onClick={() =>
                setSendNotify((prev) => ({
                  ...prev,
                  alarm_mode: "EMAIL_NOTIFICATION",
                }))
              }
            >
              <img
                src={
                  sendNotify.alarm_mode === "EMAIL_NOTIFICATION"
                    ? `${assestsUtilityUrl}/radio-blue.svg`
                    : `${assestsUtilityUrl}/radio-grey.svg`
                }
                alt="icon"
              />
              <div className={style.notify_text}>
                <h6>{t("Notify_On_Email_and_SMS")}</h6>
                <p>{t("Notify_On_Email_and_SMS_note")}</p>
              </div>
            </section>
            <FormTextarea
              label={t("student_title_label")}
              placeholder={t("student_title_label_placeholder")}
              name="m_title"
              value={sendNotify.m_title}
              type="text"
              onChange={(e) =>
                setSendNotify((prev) => ({
                  ...prev,
                  m_title: e.target.value,
                }))
              }
              errorShow={errorField["m_title"]}
            />
            <FormTextarea
              label={t("student_message_label")}
              placeholder={t("student_message_label_placeholder")}
              name="content"
              value={sendNotify.content}
              type="text"
              onChange={(e) =>
                setSendNotify((prev) => ({
                  ...prev,
                  content: e.target.value,
                }))
              }
            />
            <FormFile
              label={t("attachment")}
              name={"document"}
              value={file}
              onChange={onFileUpload}
            />
            <CreateButton label="confirm" onAction={onAction} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default DocumentReminderMessage;
